
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #F6EAFE;
  height: 80px;
  width: 100%;
  padding: 0% 20px;
  position: fixed;
  z-index: 10;
}

.navbar-logo {
  height: inherit;
  display: flex;
  align-items: center;
}

h1 {
  font-size: 1.5rem;
  font-weight: 800;
}

.navbar-links {
  display: flex;
  align-items: center;
}

.navbar-links ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 20px;
  align-items: center;
}

.navbar-links li {
  margin: 10px;
}

.navbar-links a {
  color: #333;
  text-decoration: none;
}

.navbar a {
  color: #333;
  text-decoration: none;
}

.navbar-divider {
  height: 20px;
  border-left: 1px solid black;
  margin: 0 5px;
}

.navbar-button {
  padding: 5px 10px;
  margin-left: 10px;
  font-family: inherit;
  font-weight: 600;
  font-size: 1.2rem;
  border-radius: 2%;
  background-color: rgba(0, 0, 0, 0);
  color: black;
  border: 1px solid black;
}

.logo {
  height: 40%;
  margin-left: 10%;
}

.navbar-mobile-button {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.bar {
  width: 30px;
  height: 3px;
  margin: 4px 0;
  background-color: #333;
  transition: 0.4s;
}

.bar-1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.bar-2 {
  opacity: 0;
}

.bar-3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

@media only screen and (max-width: 768px) {
  .navbar {
    height: 80px;
  }

  .navbar-logo {
    width: 30px;
  }

  .navbar-text h1 {
    font-size: 1rem;
  }

  .navbar-links ul {
    display: none;
  }

  .navbar-divider {
    display: none;
  }

  .navbar-button {
    font-family: inherit;
    font-size: 1rem;
    padding: 6px 12px;
    margin-right: 12px;
  }

  .navbar-mobile-button {
    display: flex;
  }

  .navbar.mobile-menu-open ul {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 80px;
    right: 0;
    background-color: #F6EAFE;
    width: 100%;
  }

  .navbar.mobile-menu-open ul li {
    margin: 10px;
  }
}
